
.header {
  background-color: #02a774;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  height: 45px;
}
.header .header_search {
  position: absolute;
  left: 15px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 10%;
  height: 50%;
}
.header .header_search .icon-sousuo {
  font-size: 25px;
  color: #fff;
}
.header .header_title {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 50%;
  color: #fff;
  text-align: center;
}
.header .header_title .header_title_text {
  font-size: 20px;
  color: #fff;
  display: block;
}
.header .header_login {
  font-size: 14px;
  color: #fff;
  position: absolute;
  right: 15px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.header .header_login .header_login_text {
  color: #fff;
}


