
{
  *zoom: 1;
}
@media only screen and (-webkit-device-pixel-ratio: 2) {
.border-1px::before {
    -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
}
}
@media only screen and (-webkit-device-pixel-ratio: 3) {
.border-1px::before {
    -webkit-transform: scaleY(0.333333);
            transform: scaleY(0.333333);
}
}
::after {
  content: '';
  display: block;
  clear: both;
}
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease {
  display: inline-block;
  padding: 6px;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
}
.cartcontrol .icon-remove_circle_outline {
  display: inline-block;
  padding: 6px;
  line-height: 24px;
  font-size: 24px;
  color: #02a774;
}
.cartcontrol .icon-remove_circle_outline.move-enter-active,
.cartcontrol .icon-remove_circle_outline.move-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.cartcontrol .icon-remove_circle_outline.move-enter,
.cartcontrol .icon-remove_circle_outline.move-leave-to {
  opacity: 0;
  -webkit-transform: translateX(15px) rotate(180deg);
          transform: translateX(15px) rotate(180deg);
}
.cartcontrol .cart-count {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  padding-top: 6px;
  line-height: 24px;
  text-align: center;
  font-size: 10px;
  color: #93999f;
}
.cartcontrol .icon-add_circle {
  display: inline-block;
  padding: 6px;
  line-height: 24px;
  font-size: 24px;
  color: #02a774;
}


