
{
  *zoom: 1;
}
@media only screen and (-webkit-device-pixel-ratio: 2) {
.border-1px::before {
    -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
}
}
@media only screen and (-webkit-device-pixel-ratio: 3) {
.border-1px::before {
    -webkit-transform: scaleY(0.333333);
            transform: scaleY(0.333333);
}
}
::after {
  content: '';
  display: block;
  clear: both;
}
.shop-info {
  position: absolute;
  top: 195px;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #fff;
  overflow: hidden;
}
.shop-info .section {
  padding: 16px 14px 14px;
  font-size: 16px;
  background-color: #fff;
  color: #666;
  border-bottom: 1px solid #eee;
  position: relative;
}
.shop-info .section .section-title {
  color: #000;
  font-weight: 700;
  line-height: 16px;
}
.shop-info .section .section-title > .iconfont {
  float: right;
  color: #ccc;
}
.shop-info .section .delivery {
  margin-top: 16px;
  font-size: 13px;
  line-height: 18px;
}
.shop-info .section .delivery .delivery-icon {
  width: 55px;
  font-size: 11px;
  margin-right: 10px;
  display: inline-block;
  text-align: center;
  color: #fff;
  background-color: #0097ff;
  padding: 1px 0;
  border-radius: 4px;
}
.shop-info .section .delivery .delivery-money {
  margin-top: 5px;
}
.shop-info .section .activity {
  margin-top: 16px;
}
.shop-info .section .activity .activity-item {
  margin-bottom: 12px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 13px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.shop-info .section .activity .activity-item.activity-green .content-tag {
  background-color: #70bc46;
}
.shop-info .section .activity .activity-item.activity-red .content-tag {
  background-color: #f07373;
}
.shop-info .section .activity .activity-item.activity-orange .content-tag {
  background-color: #f1884f;
}
.shop-info .section .activity .activity-item .content-tag {
  display: inline-block;
  border-radius: 2px;
  width: 36px;
  height: 18px;
  margin-right: 10px;
  color: #fff;
  font-style: normal;
  position: relative;
}
.shop-info .section .activity .activity-item .content-tag .mini-tag {
  position: absolute;
  left: 0;
  top: 0;
  right: -100%;
  bottom: -100%;
  font-size: 24px;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.shop-info .section .pic-wrapper {
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  margin-top: 16px;
}
.shop-info .section .pic-wrapper .pic-list {
  font-size: 0;
}
.shop-info .section .pic-wrapper .pic-list .pic-item {
  display: inline-block;
  margin-right: 6px;
  width: 120px;
  height: 90px;
}
.shop-info .section .pic-wrapper .pic-list .pic-item:last-child {
  margin: 0;
}
.shop-info .section .detail {
  margin-bottom: -16px;
}
.shop-info .section .detail > li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-right: -10px;
  padding: 16px 12px 16px 0;
  line-height: 16px;
  position: relative;
  border: none;
  font-size: 13px;
}
.shop-info .section .detail > li:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: #ddd;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.shop-info .section .detail > li > .bold {
  font-weight: 700;
  color: #333;
}
.shop-info .split {
  width: 100%;
  height: 16px;
  border-top: 1px solid rgba(7,17,27,0.1);
  border-bottom: 1px solid rgba(7,17,27,0.1);
  background: #f3f5f7;
}


