
{
  *zoom: 1;
}
@media only screen and (-webkit-device-pixel-ratio: 2) {
.border-1px::before {
    -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
}
}
@media only screen and (-webkit-device-pixel-ratio: 3) {
.border-1px::before {
    -webkit-transform: scaleY(0.333333);
            transform: scaleY(0.333333);
}
}
::after {
  content: '';
  display: block;
  clear: both;
}
.tab {
  height: 40px;
  line-height: 40px;
  background: #fff;
  position: relative;
  border: none;
}
.tab:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: rgba(7,17,27,0.1);
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.tab-item {
  float: left;
  width: 33.33333%;
  text-align: center;
  font-size: 14px;
  color: #4d555d;
}
a {
  display: block;
  position: relative;
}
.router-link-active {
  color: #02a774;
}
.router-link-active::after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 1px;
  width: 35px;
  height: 2px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  background: #02a774;
}


