/*引入公共样式*/
{
  *zoom: 1;
}
@media only screen and (-webkit-device-pixel-ratio: 2) {
.border-1px::before {
    -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
}
}
@media only screen and (-webkit-device-pixel-ratio: 3) {
.border-1px::before {
    -webkit-transform: scaleY(0.333333);
            transform: scaleY(0.333333);
}
}
::after {
  content: '';
  display: block;
  clear: both;
}
.footer_guide {
/*顶部有白色的边框*/
  position: relative;
  position: fixed;
  z-index: 100;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  width: 100%;
  height: 50px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.footer_guide::before {
  content: '';
  position: absolute;
  z-index: 200;
  left: 0;
  top: 0;
  width: 100%;
  height: 1px;
  background-color: #e4e4e4;
}
.footer_guide .guide_item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 5px;
  color: #999;
}
.footer_guide .guide_item.on {
  color: #02a774;
}
.footer_guide .guide_item span {
  font-size: 12px;
  margin-top: 2px;
  margin-bottom: 2px;
}
.footer_guide .guide_item span .iconfont {
  font-size: 22px;
}


